.bookmarks-container {
  width: 100%;

  padding: 1.2rem 2.4rem;
  padding-bottom: 3.6rem;
}

.bookmarks_background-container {
  list-style: none;
  width: 100%;
  height: 50rem;

  background-color: rgb(250, 250, 250);
  border-radius: 6px;
  box-shadow: 0 0 3px 0.3px #10316b1f;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.2rem;

  padding: 0.2rem 0.2rem;

  overflow-y: auto;
}

.bookmarks_background-container::-webkit-scrollbar {
  width: 1rem;
}

.bookmarks_background-container::-webkit-scrollbar-track {
  border-radius: 100vh;
  margin-block: 0.5rem;
}

.bookmarks_background-container::-webkit-scrollbar-thumb {
  /* background: #10316b; */
  background: linear-gradient(45deg, #28467a, #10316b);
  border-radius: 100vh;
}

.bookmarks_background-container::-webkit-scrollbar-thumb:hover {
  background: #586f97;
}

.bookmark_item-container {
  height: 10rem;
  width: 100%;

  padding: 1.2rem 1.2rem;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 1.2rem;

  cursor: pointer;
}

.bookmark_item-container:hover {
  /* box-shadow: 0 0 3px 0.3px rgba(0, 0, 0, 0.1); */
  box-shadow: 0 0 6px #8898b5;
}

.bookmark_item__header-container {
  height: 40%;

  display: flex;
  align-items: center;
  gap: 1.2rem;
}

.bookmark_item__header-container > img {
  height: 100%;
  width: auto;

  border-radius: 50%;
}

.bookmark_item__topic-text {
  font-size: 1.4rem;
  color: #222;
}

.bookmark_item__createdAt-text {
  margin-left: 1.2rem;

  font-size: 1.2rem;
  color: #555;
}

.bookmark_item__title-wrapper {
  width: 100%;
  height: 40%;

  padding: 0 0.4rem;
}

.bookmark_item__title-text {
  font-size: 1.6rem;
  font-weight: 600;
  color: #333;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.no_content-container {
  width: 100%;
  height: 100%;

  padding-top: 6.4rem;
}

.no_content-container > p {
  font-size: 2.4rem;
  color: #666;
  text-align: center;
}

@media (max-width: 710px) {
  .bookmarks_background-container {
    height: 45rem;
  }

  .bookmark_item-container {
    height: 8rem;

    padding: 1.2rem 1.2rem;

    gap: 0.8rem;
  }
}

@media (max-width: 600px) {
  .bookmarks_background-container {
    height: 38rem;
  }

  .bookmark_item-container {
    height: 6rem;

    padding: 1rem;

    gap: 0.8rem;
  }

  .bookmark_item__topic-text {
    font-size: 1.3rem;
  }

  .bookmark_item__createdAt-text {
    margin-left: 1.2rem;

    font-size: 1.3rem;
  }

  .bookmark_item__title-text {
    font-size: 1.4rem;
  }
}

@media (max-width: 500px) {
  .bookmark_item-container {
    height: 4.8rem;

    padding: 0.8rem;

    gap: 0.8rem;
  }

  .bookmark_item__topic-text {
    font-size: 1.2rem;
  }

  .bookmark_item__createdAt-text {
    margin-left: 1.2rem;

    font-size: 1.2rem;
  }

  .bookmark_item__title-text {
    font-size: 1.2rem;
  }
}
@media (max-width: 400px) {
  .bookmark_item-container {
    height: 4.2rem;

    padding: 0.6rem;

    gap: 0.8rem;
  }

  .bookmark_item__topic-text {
    font-size: 1.1rem;
  }

  .bookmark_item__createdAt-text {
    margin-left: 1rem;

    font-size: 1rem;
  }

  .bookmark_item__title-text {
    font-size: 1.1rem;
  }
}

@media (max-width: 380px) {
  .bookmark_item__createdAt-text {
    display: none;
  }
}
